.clearfix:after::after {
  display: block;
  content: "";
  clear: both;
}

.wrapper {
  max-width: 1276px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

html {
  font-size: 100%;
}

body {
  font-family: Rubik, Arial, sans-serif;
  font-size: 20px;
  line-height: 1.375;
  font-weight: 400;
  color: #111111;
}

p,
ul,
li {
  margin: 0;
  padding: 0;
}

.btn-call {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 22px 0 7px;
  border: none;
  background: #B7000E;
  border-radius: 4px;
  font-family: Rubik, Arial, sans-serif;
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  color: #ffffff;
  cursor: pointer;
  transition: background ease 0.5s;
}

.btn-call:hover {
  background: #ea0012;
}

.btn-call:active {
  background: #84000a;
}

.hide {
  display: none;
}

