/*=============================================
        ШАПКА
=============================================*/

.top-line {
  max-width: calc(100% - 38px);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 0 28px 0;
}

.top-line__block {
  font-size: 17px;
  line-height: 25px;
  color: #aaaaaa;
}

.top-line__block1 > div,
.top-line__block4 > div {
  display: flex;
}

.top-line__social-box {
  margin-left: 12px;
}

.top-line__social-box .icon-viber.icon-viber--mob {
  display: none;
}

.top-line__tel {
  display: block;
  font-size: 20px;
  color: #111111;
}

.top-line__email {
  color: #B7000E;
  font-size: 17px;
}

.top-line__adress {
  font-style: normal;
}

.top-line__block4 {
  display: none;
}

.logo {
  display: block;
}

.logo__title {
  text-align: center;
  font-size: 15px;
  line-height: 18px;
  color: #111111;
}

.nav-line {
  padding: 10px 0;
  background: #111111;
  position: relative;
}

.nav-line__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.burger {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 15px;
  padding: 0;
  border: none;
  background: transparent;
  cursor: pointer;
}

.burger__icon {
  position: relative;
  display: block;
  width: 20px;
  height: 2px;
  border-radius: 3px;
  background-color: #ffffff;
}

.burger__icon::before {
  position: absolute;
  left: 0;
  top: -6px;
  content: '';
  width: 20px;
  height: 2px;
  border-radius: 3px;
  background-color: #ffffff;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
}

.burger__icon::after {
  position: absolute;
  left: 0;
  top: 6px;
  content: '';
  width: 20px;
  height: 2px;
  border-radius: 3px;
  background-color: #ffffff;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
}

.burger__icon.burger__icon--active {
  background-color: transparent;
}

.burger__icon.burger__icon--active::before {
  transform: rotate(45deg);
  top: 0;
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
}

.burger__icon.burger__icon--active::after {
  transform: rotate(-45deg);
  top: 0;
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav__burger {
  z-index: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav__burger P {
  display: none;
  font-weight: 700;
  font-size: 15px;
  line-height: 18px;
  color: #ffffff;
}

.nav__list {
  display: flex;
  align-items: center;
  list-style-type: none;
}

.nav__item {
  margin-left: 65px;
}

.nav__item:first-child {
  position: relative;
  margin-left: 12px;
}

.nav__item:first-child span {
  cursor: default;
}

.nav__link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 22px;
  line-height: 26px;
  font-weight: 500;
  color: #ffffff;
  transition: color ease 0.5s;
}

.nav__link:hover {
  color: #ea0012;
}

.nav__link.active {
  color: #84000a;
}

.nav__link--drop {
  width: 100%;
  background: transparent;
  border: none;
}

.nav__link svg {
  display: none;
}

.nav__submenu {
  position: absolute;
  left: -10px;
  top: 100%;
  width: 265%;
  padding: 20px 10px 20px;
  background: #111111;
  list-style: none;
  transition: opacity 0.5s ease-in;
  height: auto;
  overflow: visible;
  opacity: 1;
}

.nav__submenu--visial {
  height: 0;
  overflow: hidden;
  opacity: 0;
  transition: opacity 0.1s ease-in;
}

.nav__subitem svg {
  display: none;
}

.nav__sublink {
  font-size: 1.17vw;
  line-height: 24px;
  font-weight: 500;
  color: #ffffff;
}

.nav__sublink:hover {
  color: #ea0012;
}

.nav__sublink--disabled {
  pointer-events: none;
  cursor: default;
  color: #999;
}

.nav__sublink span:hover {
  cursor: pointer;
}

.nav__btn--submenu {
  display: none;
}

.nav__submenu2 {
  padding-left: 20px;
  list-style-type: none;
  background: #EEEEEE;
  transition: opacity 0.1s ease-in;
  height: auto;
  overflow: visible;
  opacity: 1;
}

.nav__submenu2--visial {
  height: 0;
  overflow: hidden;
  opacity: 0;
  transition: opacity 0.1s ease-in;
}

