@media screen and (max-width: 1250px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .nav__item {
    margin-left: 5vw;
  }

  .nav__link {
    font-size: 1.6vw;
  }
}

@media screen and (max-width: 1000px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .top-line {
    max-width: calc(100% - 19px);
    padding: 20px 0 28px 0;
  }

  .top-line__block {
    font-size: 15px;
  }

  .top-line__social-box .icon-viber {
    display: none;
  }

  .top-line__social-box .icon-viber.icon-viber--mob {
    display: inline-block;
  }

  .top-line__tel {
    font-size: 17px;
  }

  .top-line__email {
    font-size: 15px;
  }

  .logo__title {
    font-size: 13px;
  }

  .nav__item {
    margin-left: 4vw;
  }

  .nav__link {
    font-size: 1.7vw;
    line-height: 20px;
  }

  .nav__sublink {
    font-size: 1.5vw;
    line-height: 22px;
  }
}

@media screen and (max-width: 890px) {
  .btn-call {
    padding: 11px 20px 13px 20px;
    font-size: 13px;
    line-height: 15px;
  }

  .btn-call img {
    display: none;
  }

  .top-line__block3 {
    display: none;
  }
}

@media screen and (max-width: 768px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .top-line {
    max-width: calc(100% - 10px);
  }

  .nav__item {
    margin-left: 3vw;
  }

  .nav__link {
    font-size: 1.95vw;
    line-height: 18px;
  }

  .nav__sublink {
    font-size: 14px;
  }
}

@media screen and (max-width: 700px) {
  .btn-call {
    padding: 0 22px 0 7px;
  }

  .btn-call img {
    display: flex;
  }

  .top-line__block1,
  .top-line__block2,
  .top-line__block3 {
    display: none;
  }

  .top-line__block4 {
    display: block;
  }

  .nav__burger p {
    display: block;
    margin-left: 10px;
  }

  .nav__list {
    position: absolute;
    top: 100%;
    left: 0;
    display: block;
    width: 100%;
    background: #111111;
    transition: opacity 0.5s ease-in;
    height: auto;
    overflow: visible;
    opacity: 1;
  }

  .nav__list--visial {
    height: 0;
    overflow: hidden;
    opacity: 0;
    transition: opacity 0.1s ease-in;
  }

  .nav__item {
    margin: 0;
    padding: 20px 0 20px 20px;
    border-bottom: 1px solid rgba(119, 118, 118, 0.55);
  }

  .nav__item:first-child {
    margin-left: 0;
  }

  .nav__item--drop {
    padding: 20px 0 0 0;
  }

  .nav__link {
    font-size: 20px;
    line-height: 24px;
  }

  .nav__link svg {
    display: block;
    width: 24px;
    height: 12px;
    fill: #CCCCCC;
    margin-right: 20px;
  }

  .nav__link svg:hover {
    cursor: pointer;
  }

  .nav__link--drop {
    margin: 0 0 20px 0;
    padding-left: 20px;
  }

  .nav__link--drop svg.active {
    fill: #B7000E;
    transform: rotate(180deg);
  }

  .nav__submenu {
    position: static;
    width: 100%;
    padding: 0;
  }

  .nav__subitem {
    background: #ffffff;
    box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.05);
    border-bottom: 1px solid rgba(119, 118, 118, 0.55);
  }

  .nav__subitem:focus {
    background: #B7000E;
  }

  .nav__subitem:focus .nav__sublink {
    color: #ffffff;
  }

  .nav__subitem:hover {
    background: #B7000E;
  }

  .nav__subitem div {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .nav__subitem svg {
    display: inline-block;
    width: 24px;
    height: 12px;
    fill: #CCCCCC;
    margin-right: 20px;
  }

  .nav__subitem svg:hover {
    cursor: pointer;
  }

  .nav__subitem svg.active {
    fill: #ffffff;
    transform: rotate(180deg);
  }

  .nav__sublink {
    padding: 20px;
    font-weight: 400;
    font-size: 17px;
    line-height: 125.19%;
    color: #111111;
  }

  .nav__sublink:focus {
    color: #ffffff;
  }

  .nav__sublink--disabled {
    pointer-events: none;
    cursor: default;
    color: #999;
  }

  .nav__btn--submenu {
    display: inline-block;
    background: transparent;
    border: none;
  }

  .nav__subitem2 {
    padding: 5px 0;
  }

  .nav__link2 {
    font-family: Roboto;
    font-weight: 700;
    font-size: 17px;
    line-height: 20px;
    color: #111111;
  }

  .nav__link2:hover,
  .nav__link2:focus {
    color: #B7000E;
  }

  .nav__link2--disabled {
    pointer-events: none;
    cursor: default;
    color: #999;
  }
}

@media screen and (max-width: 480px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .top-line {
    padding-bottom: 23px;
  }

  .top-line__tel {
    font-size: 15px;
    line-height: 24px;
  }
}

@media screen and (max-width: 440px) {
  .top-line__block4 .top-line__social-box {
    display: none;
  }

  .logo__title {
    font-size: 9px;
    line-height: 10px;
  }
}

@media screen and (max-width: 320px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }

  .top-line {
    max-width: calc(100% - 2px);
    padding: 12px 0 15px 0;
  }

  .logo__title {
    font-size: 9px;
    line-height: 5px;
  }
}